import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      oneMax: '',
      twoMax: '',
      lastNum: ''
    }
  },
  created () {
    this.oneMax = this.ONE_MAX
    this.twoMax = this.TWO_MAX
    this.lastNum = this.LAST_NUM
  },
  computed: {
    ...mapGetters([
      'DONE_RECORDING'
    ])
  },
  methods: {
    _clearAll () {
      this.$store.dispatch('CLEAR_ALL')
      this.$vux.toast.show({
        text: '    已清空记录     '
      })
    }
  },
  components: {
  }
}



