import { mapState } from 'vuex'
import Group from 'vux/src/components/group/'
import XInput from 'vux/src/components/x-input/'
import XSwitch from 'vux/src/components/x-switch/'

export default {
  data () {
    return {
      isSwitch: false,
      times: '',
      oneSet: '',
      twoSet: ''
    }
  },
  created () {
    this.times = this.customize.times
    this.oneSet = this.customize.oneSet
    this.twoSet = this.customize.twoSet
    this.isSwitch = this.customize.switch
  },
  computed: {
    ...mapState({
      customize: state => state.customize
    })
  },
  methods: {
    _submitCarry () {
      this.$store.dispatch('ADMIN_SAVE_SET', { times: this.times, oneSet: this.oneSet, twoSet: this.customize.twoSet, switch: this.isSwitch })
      this.$vux.toast.show({
        text: '    保存成功     '
      })
    }
  },
  components: {
    Group,
    XInput,
    XSwitch
  }
}



